import { Component, h, Element, Event, EventEmitter, State, Listen } from '@stencil/core/internal';
import tailwind from '../../output.css';
import { data } from '../../data';
@Component({
  tag: 'rf-navbar',
  shadow: true,
})
export class Navbar {
  @Element() element: HTMLElement;

  hotel = data.My_Result;

  componentDidLoad() {
    const shadowRoot = this.element.shadowRoot;
    if (shadowRoot) {
      const style = document.createElement('style');
      style.textContent = tailwind;
      shadowRoot.appendChild(style);
    }
  }

  @Event({ bubbles: true, composed: true }) rf_show_popup: EventEmitter<boolean>;
  show(){
    console.log('hello')
    this.rf_show_popup.emit(true)
  }

  @State() language = 'English';
  @State() currency = 'US$';

  @Listen('rf_send_data', { target: 'body' })
   getData(event: CustomEvent){
    this.language = event.detail.lang;
    this.currency = event.detail.cur;
   }
  render() {
    return (
        <nav class="flex flex-row justify-between px-4 lg:px-20 xl:px-60 py-3 fixed top-0 left-0 w-full bg-white z-10">
        <div class='flex flex-col'>
          <div class='font-bold text-xl'>{this.hotel.name}</div>
          <small class='text-gray-600 text-base'>{this.hotel.area + ', ' + this.hotel.city.name + ', ' + this.hotel.country.name}</small>
        </div>
        <div class="flex flex-row gap-2 md:gap-10 items-center text-gray-600 font-semibold">
          <button class='hidden md:block hover:bg-gray-300 p-2 hover:rounded-full'>Booking code</button>
          <button class='hover:bg-gray-300 p-2 hover:rounded-full' onClick={this.show.bind(this)}>{this.currency}</button>
          <button class='hover:bg-gray-300 p-2 hover:rounded-full' onClick={this.show.bind(this)}>{this.language}</button>
          <button class='hidden md:block hover:bg-gray-300 p-2 hover:rounded-full'>Sign in</button>
        </div>
      </nav>
    );
  }
}
